@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*  Rubik font */

html {
    /* custom colors */
    --dark-orange: rgb(179, 86, 11);
    --very-dark-orange: rgb(160, 80, 15);
    --light-orange: rgba(185, 127, 80, 0.568);
    --light-grey: rgba(123, 175, 158, 0.486);
    --dark-grey: rgb(57, 82, 74);
    --dark-green: rgba(28, 129, 21, 0.678);
}

html, body {
    width: 100%;
    height: 100%;
    margin: 0px;
    padding: 0px;
    overflow-x: hidden;

    font-family: 'Rubik', sans-serif;
    background-color: rgba(123, 175, 158, 0.123);
}


h1#headline { /* Title (Sorting Visualizer) */
    text-align: center;
    background-color: rgba(123, 175, 158, 0.486);
    background-color: var(--light-grey);
    margin: 0 0;
    padding-top: 10px;
    padding-bottom: 10px;
}

div#sort-buttons-container {
    margin: 10px 0;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
}


div#array-changer-container{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
}

div#size-changer-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: nowrap;
            flex-wrap: nowrap;
    -webkit-align-items: center;
            align-items: center;
}

div#wrapper {
    display:-webkit-flex;
    display:flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

#visualizer-container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: baseline;
            align-items: baseline;
    max-height: 80vh;
    min-height: 30vh;
    margin: 60px 200px;
}

div#sorting-info {
    position: relative;
    top: 50px;
    left: 200px;
}

.array-bar { /* vertical bar representing a value of the array */ 
    display: -webkit-flex; 
    display: flex;
    -webkit-flex: 1 1;
            flex: 1 1;
    background-color: rgba(185, 127, 80, 0.568);
    background-color: var(--light-orange);
    margin: 0 1px;
    -webkit-justify-content: center;
            justify-content: center;
}

.array-bar:hover {
    background-color: rgb(179, 86, 11);
    background-color: var(--dark-orange);
    box-shadow: 0 0 15px rgb(57, 82, 74);
    box-shadow: 0 0 15px var(--dark-grey);
}

/* Buttons */

.custom-button {
    margin: 2px 8px;
    border-radius: 5px;
    font-size: 16px;
    border: none;
    padding: 6px 12px;
}

.custom-button:hover:enabled {
    cursor: pointer;
    box-shadow: 0 0 15px rgb(57, 82, 74);
    box-shadow: 0 0 15px var(--dark-grey);
}



.custom-button:disabled {
    background-color: lightgrey;
    color: darkgrey;
}

.sort-button {
    background-color: rgb(179, 86, 11);
    background-color: var(--dark-orange);
    color: white;
}
/*
.sort-button:hover:enabled, .sort-button:focus:enabled {
    
}

.reset-button:hover:enabled, .reset-button:focus:enabled {

}
*/

.reset-button {
    background-color: rgb(57, 82, 74);
    background-color: var(--dark-grey);
    color: white;
    margin-left: 10px;
    margin-right: 10px;
}

.custom-button:active:enabled {
    box-shadow: 0 0 15px rgb(57, 82, 74);
    box-shadow: 0 0 15px var(--dark-grey);
}

.size-button {
    width: 40px;
    height: 30px;
}

.size-button:disabled {
    background-color: lightgrey;
    color: darkgrey;
}

.minus-size-button {
    background-color: rgba(179, 0, 0, 0.753);
    color: white;
}

.plus-size-button {
    background-color: rgba(12, 109, 44, 0.705);
    color: white;;
}


@media only screen and (max-width: 768px) {
    div#visualizer-container {
      margin-right: 10px;
      margin-left: 10px;
    }

    div#sorting-info {
        left: 20px;
    }
}
